import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import { Links } from './properties/links';

const routes: Routes = [
    { path: '', redirectTo: 'home', pathMatch: 'full' },
    { path: Links.home, loadChildren: () => import('./home/home.module').then(m => m.HomePageModule) },
    { path: Links.assemblyman,
                        loadChildren: './components/asambleistas/asambleistas.module#AsambleistasPageModule' },
    { path: Links.votations, loadChildren: './components/votaciones/votaciones.module#VotacionesPageModule' },
    { path: Links.sessions, loadChildren: './components/sesiones/sesiones.module#SesionesPageModule' },
    { path: Links.themes, loadChildren: './components/temas/temas.module#TemasPageModule' },
    { path: Links.favorites, loadChildren: './components/favoritos/favoritos.module#FavoritosPageModule' },
    { path: Links.about, loadChildren: './components/about/about.module#AboutPageModule' },
    { path: Links.structure,
        loadChildren: './components/estructura-legislativa/estructura-legislativa.module#EstructuraLegislativaPageModule' },
    // { path: 'modal-order-aproved', loadChildren: './components/modal/modal-order-aproved/modal-order-aproved.module#ModalOrderAprovedPageModule' },
  // { path: 'modal-intervening', loadChildren: './components/modal/modal-intervening/modal-intervening.module#ModalInterveningPageModule' },
  // { path: 'modal-voting-detail', loadChildren: './components/modal/modal-voting-detail/modal-voting-detail.module#ModalVotingDetailPageModule' },
  { path: 'tab-sesiones', loadChildren: './components/tabs/tab-sesiones/tab-sesiones.module#TabSesionesPageModule' },
  { path: 'tab-temas', loadChildren: './components/tabs/tab-temas/tab-temas.module#TabTemasPageModule' },
  { path: 'ppless', loadChildren: './components/external/ppless/ppless.module#PplessPageModule' },
  { path: 'laws', loadChildren: './components/external/laws/laws.module#LawsPageModule' },
  // { path: 'modal-statistics', loadChildren: './components/modal/modal-statistics/modal-statistics.module#ModalStatisticsPageModule' },
  // { path: 'intervening-detail-assemblyman', loadChildren: './components/modal/intervening-detail-assemblyman/intervening-detail-assemblyman.module#InterveningDetailAssemblymanPageModule' },
  // { path: 'voting-detail-assemblyman', loadChildren: './components/modal/voting-detail-assemblyman/voting-detail-assemblyman.module#VotingDetailAssemblymanPageModule' },
  { path: 'tab-principales', loadChildren: './components/tabs/tab-principales/tab-principales.module#TabPrincipalesPageModule' },
  { path: 'tab-alternos', loadChildren: './components/tabs/tab-alternos/tab-alternos.module#TabAlternosPageModule' },
  // { path: 'modal-asambleista-quorums', loadChildren: './components/modal/modal-asambleista-quorums/modal-asambleista-quorums.module#ModalAsambleistaQuorumsPageModule' },
  // { path: 'tvonline', loadChildren: './components/external/tvonline/tvonline.module#TvonlinePageModule' },
  { path: 'tourvirtual', loadChildren: './components/external/tourvirtual/tourvirtual.module#TourvirtualPageModule' },
  { path: 'inspection', loadChildren: './components/external/inspection/inspection.module#InspectionPageModule' },
  { path: 'assemblyhouses', loadChildren: './components/external/assemblyhouses/assemblyhouses.module#AssemblyhousesPageModule' },
  //{ path: 'facebook', loadChildren: './components/external/facebook/facebook.module#FacebookPageModule' },
  // { path: 'radioonline', loadChildren: './components/external/radioonline/radioonline.module#RadioonlinePageModule' },
    // { path: 'testing', loadChildren: './components/testing/testing.module#TestingPageModule' },
];

@NgModule({
    imports: [
        RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
    ],
    exports: [RouterModule]
})
export class AppRoutingModule { }
