import { Component, OnInit } from '@angular/core';
import {ModalController} from "@ionic/angular";
import {ScreenOrientation} from "@ionic-native/screen-orientation/ngx";
import {EnvironmentService} from "../../../service/environment.service";
import {FacebookLive} from "../../../interfaces/model";
import {DomSanitizer} from "@angular/platform-browser";
import {LoadingService} from "../../../service/loading.service";

@Component({
  selector: 'app-facebook',
  templateUrl: './facebook.page.html',
  styleUrls: ['./facebook.page.scss'],
})
export class FacebookPage implements OnInit {
  url: any = '';
  videoList: FacebookLive[] = [];
  viewVideo: boolean = false;
  meeting: string = '';

  constructor(private modalController: ModalController,
              private screenOrientation: ScreenOrientation,
              private sanitizer: DomSanitizer,
              public  environmentService: EnvironmentService,
              private loadingService: LoadingService) { }

  ngOnInit() {
    this.environmentService.getFacebookList().subscribe(value => {
      this.videoList = value;
      /*
      if( !this.viewVideo ) {
        this.url = '';
        if( value != null ) {
          this.videoList = value;
          if ( value.length == 1 ) {
            this.url = this.getSanizeUrl(value[0].url);
            this.meeting = value[0].meetinggroup;
            this.viewVideo = true;
          }

          if ( value.length == 0 || value.length > 1) {
            this.viewVideo = false;
            this.meeting = '';
            this.closeLoader();
          }
        }else {
          this.viewVideo = false;
          this.videoList = null;
          this.meeting = '';
          this.closeLoader()
        }
      }*/
    });
  }

  closeLoader(){
    this.loadingService.dismissLoader();
  }

  onSelectMeetingGroup(f: FacebookLive) {
    this.loadingService.presentLoader();
    this.url = this.getSanizeUrl(f.url);
    this.meeting = f.meetinggroup;
    this.viewVideo = true;
  }

  getSanizeUrl(idVideo: string) {
    // const s = 'https://www.facebook.com/plugins/video.php?' +
        // 'href=https%3A%2F%2Fwww.facebook.com%2Fasambleanacional%2Fvideos%2F' +
        // idVideo + '%2F&show_text=0';

    const s = 'https://www.facebook.com/plugins/video.php?href=' + idVideo;
    return this.sanitizer.bypassSecurityTrustResourceUrl(s);
  }

  closeVideoView() {
    this.viewVideo = false;
    this.meeting = '';
    this.closeLoader();
  }

  async closeModal() {
    await this.modalController.dismiss();
  }

}
