import { Component, OnInit } from '@angular/core';
import { ModalController, NavParams} from '@ionic/angular';
import { RestService } from '../../../service/rest.service';
import {Vote, Voting} from '../../../interfaces/model';

@Component({
    selector: 'app-modal-voting-detail',
    templateUrl: './modal-voting-detail.page.html',
    styleUrls: ['./modal-voting-detail.page.scss'],
})
export class ModalVotingDetailPage implements OnInit {

    votingId: number;
    voting: Voting;
    graphic: any;
    votes: Vote[] = [];
    activeCss = false;

    votesYes: Vote[] = [];
    votesNo: Vote[] = [];
    votesAbstention: Vote[] = [];
    votesWhite: Vote[] = [];

    colorYes = '#00FF00';
    colorNo = '#FF0000';
    colorAbstention = '#005EFF';
    colorWhite = '#FFFFFF';

    constructor(private modalController: ModalController,
                private navParams: NavParams,
                private rest: RestService) { }

    ngOnInit() {
        this.votingId = this.navParams.data.votingId;
        this.getDetail();
        this.getVotes();
    }

    async closeModal() {
        await this.modalController.dismiss();
    }

    getDetail() {
        this.rest.getVotingDetail({
            votingId: this.votingId
        }).subscribe( response => {
                this.voting = response;
                const blob = new Blob([this.voting.graphic], {
                    type: 'image/png'
                });
                blob.size > 300000 ? this.activeCss = false : this.activeCss = true;

                const uints = new Uint8Array(this.voting.graphic);
                let binary = '';
                for (let i = 0; i < uints.length; i++) {
                    binary += String.fromCharCode(uints[i]);
                }
                const base64 = btoa(binary);
                this.graphic = 'data:image/png;base64,' + base64;

            }
        );
    }

    getVotes() {
        this.rest.getVotingVotes({
            votingId: this.votingId
            }).subscribe( response => { this.votes = response; this.groupVotes(); }
        );
    }

    groupVotes() {
        this.votes.forEach(data => {
          switch (data.description) {
              case 'SI':
                  this.votesYes.push(data);
                  break;
              case 'NO':
                  this.votesNo.push(data);
                  break;
              case 'ABSTENCION':
                  this.votesAbstention.push(data);
                  break;
              case 'BLANCO':
                  this.votesWhite.push(data);
                  break;
          }
        });
    }

}
