import { Injectable } from '@angular/core';
import {BehaviorSubject, Observable} from 'rxjs';
import {FacebookLive} from "../interfaces/model";

@Injectable({
    providedIn: 'root'
})

export class EnvironmentService {
    private observableFacebookId: BehaviorSubject<string>;
    private observableListFacebookLive: BehaviorSubject<FacebookLive[]>;
    private observableShowRadio:BehaviorSubject<boolean>;

    constructor() {
        this.observableFacebookId = new BehaviorSubject<string>('');
        this.observableListFacebookLive = new BehaviorSubject<FacebookLive[]>(null);
        this.observableShowRadio = new BehaviorSubject<boolean>(false);
    }

    public setIdFacebook(id: string) {
        this.observableFacebookId.next(id);
    }

    public getFacebookId(): Observable<string> {
        return this.observableFacebookId.asObservable();
    }

    public setFacebookList(l: FacebookLive[]) {
        this.observableListFacebookLive.next(l);
    }

    public getFacebookList(): Observable<FacebookLive[]> {
        return this.observableListFacebookLive.asObservable();
    }

    public setShowRadio(o: boolean) {
        this.observableShowRadio.next(o);
    }

    public getShowRadio(): Observable<boolean> {
        return this.observableShowRadio.asObservable();
    }

}
