import {Component, Input, OnInit} from '@angular/core';
import {ModalController} from '@ionic/angular';
import {RestService} from '../../../service/rest.service';
import {Period, VotingAssembly} from '../../../interfaces/model';

@Component({
  selector: 'app-voting-detail-assemblyman',
  templateUrl: './voting-detail-assemblyman.page.html',
  styleUrls: ['./voting-detail-assemblyman.page.scss'],
})
export class VotingDetailAssemblymanPage implements OnInit {
    @Input() idAssembly: number;
    @Input() nameAssembly: string;
    @Input() period: number;
    i = 0;
    j = 0;
    votingList: VotingAssembly[] = [];
    date: string;
    banSend: boolean [] = [];
    ban = false;
    textIncomplete: string [] = [];
    themeFilter: string = '';

    constructor(public modalController: ModalController,
              private rest: RestService) { }

  ngOnInit() {
      this.getVotingDetail();
  }
  public loadData(event) {
        setTimeout(() => {
            this.getVotingDetail(event);
        }, 500);
  }
  banAction(pos) {
        this.banSend[pos] = !this.banSend[pos];
  }
  getVotingDetail(event?) {
      if(this.period === null || this.period === undefined){
          this.period = 0;
      }

      if(this.themeFilter === null || this.themeFilter === undefined){
          this.themeFilter = '';
      }

      this.ban = false;
      this.j = 0;
      const incre = 30;
      if (event !== undefined) {
          this.i = this.i + incre;
      }
      this.rest.getDetailVoting({
          periodId: this.period,
          assemblyMemberId: this.idAssembly,
          description: this.themeFilter,
          offset: this.i,
          limit: incre
      }).subscribe(response => {
          if (response.length > 0) {
              if (event !== undefined) {
                  this.votingList.push(...response);
                  this.votingList.forEach(data => {
                      this.date = data.votingDate.replace('[UTC]', '');
                      data['votDate'] = new Date(this.date);
                      this.banSend[this.j] = false;
                      this.textIncomplete[this.j] = data['proposalDescription'].substring(0, 100);
                      this.j++;
                  });
                  if (event) {
                      event.target.complete();
                  }
              } else {
                  this.votingList = response;
                  this.votingList.forEach(data => {
                      this.date = data.votingDate.replace('[UTC]', '');
                      data['votDate'] = new Date(this.date);
                      this.banSend[this.j] = false;
                      this.textIncomplete[this.j] = data['proposalDescription'].substring(0, 100);
                      this.j++;
                  });
              }
          } else {
              this.ban = true;
              if (response.length === 0) {
                  event.target.disabled = true;
              }
          }
      });
  }
  modalClose() {
        this.modalController.dismiss({
            'dismissed': true
        });
  }

    cleanThemeFilter(){
        this.themeFilter = '';
        this.getVotingDetail(undefined);
    }

}
