import { Injectable } from '@angular/core';
import {Observable} from 'rxjs';
import {HttpClient} from '@angular/common/http';
import {environment} from '../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class RestService {
    private urlApp = environment.urlRestApp;
  constructor(private http: HttpClient) {
  }
    getThemes(sendData: any): Observable<any> {
        return this.http.get<any>(this.urlApp + '/themesResource?id=' + sendData.id + '&search=' +
            '' + sendData.search + '&sessionNumber=' + sendData.sessionNumber + '&dateFrom='
            + sendData.dateFrom + '&dateTo=' + sendData.dateTo  + '&offset=' + sendData.offset + '&limit=30');
    }
    getAssemblymanPhoto(sendData: any): Observable<any> {
        return this.http.get(this.urlApp + '/assemblyMembersResource/getPhoto/' + sendData.assemblyMemberId, { responseType : 'blob'});
    }
    getAttachmentsFromTheme(sendData: any): Observable<any> {
      if (sendData.themeId) {
          return this.http.get<any>(this.urlApp + '/agendasResource/findAttachments/' + sendData.sessionId + '/' + sendData.themeId);
      } else {
          return this.http.get<any>(this.urlApp + '/agendasResource/findAttachments/' + sendData.sessionId);
      }
    }
    getAttachment(sendData: any): Observable<any> {
        return this.http.get(this.urlApp + '/agendasResource/download?attachmentId=' + sendData.attachmentId, { responseType : 'blob'});
    }
    getListSessions(sendData: any): Observable<any> {
        return this.http.get(this.urlApp + '/agendasResource/getList?' +
            '&sessionNumber=' + sendData.sessionNumber +
            '&from=' + sendData.from +
            '&to=' + sendData.to +
            '&offset=' + sendData.offset +
            '&limit=15');
    }
    getListThemesBySession(sendData: any): Observable<any> {
        return this.http.get(this.urlApp + '/themesResource/find1?agendaId=' + sendData.agendaId + '&date=' + sendData.date);
    }
    getOrderApproved(sendData: any): Observable<any> {
        return this.http.get<any>(this.urlApp + '/themesResource/find?agendaId=' + sendData.agendaId + '&date=' + sendData.date);
    }
    getInterveningByTheme(sendData: any): Observable<any> {
      return this.http.get(this.urlApp + '/themesResource/findSpeechRequests?themeId=' + sendData.themeId);
    }
    getListVotation(sendData: any): Observable<any> {
        return this.http.get<any>(this.urlApp + '/votingsResource/getList?id=' + sendData.id + '&sessionNumber=' + sendData.sessionNumber +
            '&dateFrom=' + sendData.dateFrom + '&dateTo=' + sendData.dateTo +
            '&search=' + sendData.search + '&meetingGroupId=0&offset=' + sendData.offset + '&limit=30');
    }
    getVotationAttachment(sendData: any): Observable<any> {
        return this.http.get(this.urlApp + '/votingsResource/generatePDF?votingId=' + sendData.votingId, { responseType : 'blob'});
    }
    getVotingDetail(sendData: any): Observable<any> {
        return this.http.get(this.urlApp + '/votingsResource/findVotingGraphic/' + sendData.votingId);
    }
    getVotingVotes(sendData: any): Observable<any> {
        return this.http.get<any>(this.urlApp + '/votingsResource/findVotes/' + sendData.votingId);
    }
    getVotingVotesByPoliticalParty(sendData: any): Observable<any> {
        return this.http.get<any>(this.urlApp + '/votingsResource/findVotingByPoliticalParties/' + sendData.votingId);
    }
    getTerritorialList(): Observable<any> {
        return this.http.get<any>(this.urlApp + '/periodsResource/territorialDivision/');
    }
    getCommissionList(sendData: any): Observable<any> {
        return this.http.get<any>(this.urlApp + '/periodsResource/' + sendData.periodId + '/meetingGroups/' + sendData.meetingGroupTypeId);
    }
    getAssemblymanByCommission(sendData: any): Observable<any> {
        return this.http.get<any>(this.urlApp + '/meetingGroupsResource/' + sendData.meetingGroupId + '/assemblyMembersNoPic/');
    }
    getCommissionsAssemblyman(sendData: any): Observable<any> {
        return this.http.get<any>(this.urlApp + '/assemblyMembersResource/findCommission/' + sendData.assemblyMemberId);
    }
    getParameterValueFromName(sendData: any): Observable<any> {
        return this.http.get<any>(this.urlApp + '/systemParametersResource/findSystemParameters?pattern=' + sendData.pattern);
    }
    getDetailVoting(sendData: any): Observable<any> {
        return this.http.get<any>(this.urlApp + '/assemblyMembersResource/findVotings?periodId=' + sendData.periodId
            + '&assemblyMemberId=' + sendData.assemblyMemberId + '&description=' + sendData.description
            + '&offset=' + sendData.offset + '&limit=' + sendData.limit);
    }
    getDetailIntervening(sendData: any): Observable<any> {
        return this.http.get<any>(this.urlApp + '/assemblyMembersResource/findSpeechRequest?periodId=' + sendData.periodId
            + '&assemblyMemberId=' + sendData.assemblyMemberId + '&description=' + sendData.description
            + '&offset=' + sendData.offset + '&limit=' + sendData.limit);
    }
    getQuorumsByAssembly(sendData: any): Observable<any> {
        return this.http.get<any>(this.urlApp + '/assemblyMembersResource/findQuorums?periodId=' + sendData.periodId
            + '&assemblyMemberId=' + sendData.assemblyMemberId + '&session=' + sendData.session
            + '&offset=' + sendData.offset + '&limit=' + sendData.limit);
    }
    getPeriod(): Observable<any> {
        return this.http.get<any>(this.urlApp + '/periodsResource/findActivePeriod');
    }
    getPeriodAll(): Observable<any> {
        return this.http.get<any>(this.urlApp + '/periodsResource/getAll');
    }
    getAssemblyMembersResource(sendData: any): Observable<any> {
      return this.http.get(this.urlApp + '/assemblyMembersResource/find/' + sendData.periodId + '/' + sendData.territorialId + '/' +
          sendData.politicalPartyId + '/' + sendData.includePicture + '/' + sendData.onlyActives + '/' + sendData.onlyAlterns + '/' +
          sendData.assembly + '/' + sendData.offset + '/' + sendData.limit);
    }
    getAssemblyHouses(): Observable<any> {
        return this.http.get<any>(this.urlApp + '/assemblyHouseResource/getAll');
    }
    getInterveningBySession(sendData: any): Observable<any> {
        return this.http.get(this.urlApp + '/agendasResource/findSpeechRequests?agendaId=' + sendData.agendaId + '&date=' + sendData.date );
    }
    getFacebookLive(): Observable<any> {
        return this.http.get<any>(this.urlApp + '/facebookLiveResource/getAll');
    }
}
