import {Component, Input, OnInit} from '@angular/core';
import {ModalController, NavParams} from '@ionic/angular';
import {RestService} from '../../../service/rest.service';
import {AttachmentTheme} from '../../../interfaces/model';
import { File, FileEntry } from '@ionic-native/file/ngx';
import { ToastController } from '@ionic/angular';
import { FileOpener } from '@ionic-native/file-opener/ngx';
import {environment} from '../../../../environments/environment';

@Component({
  selector: 'app-modal-attachment',
  templateUrl: './modal-attachment.page.html',
  styleUrls: ['./modal-attachment.page.scss'],
})
export class ModalAttachmentPage implements OnInit {

  sessionId: number;
  themeId: number;
  attList: AttachmentTheme[];
  isIos = false;
  banMessage = false;

  constructor(private modalController: ModalController,
              private navParams: NavParams,
              private rest: RestService,
              private file: File,
              private fileOpener: FileOpener,
              public toastController: ToastController) { }

  ngOnInit() {
      this.sessionId = this.navParams.data.sessionId;
      this.themeId = this.navParams.data.themeId;
      this.getAttachments();
  }
  async closeModal() {
    await this.modalController.dismiss();
  }

   private getAttachments() {
       this.rest.getAttachmentsFromTheme({
           sessionId: this.sessionId,
           themeId: this.themeId
       }).subscribe( response => {
           this.attList = response;
           if (this.attList.length === 0) {
               this.banMessage = true;
           }

       });
   }

    getAttachment(a: AttachmentTheme) {
        this.rest.getAttachment({
            attachmentId: a.id
        }).subscribe( response => {
            const blob = new Blob([response], {type: 'application/pdf'});
            /*
            const downloadLink = document.createElement('a');
            downloadLink.href = window.URL.createObjectURL(blob);
            downloadLink.download = a.name + '.pdf';
            downloadLink.click();
            */
            let filePath = this.file.externalRootDirectory + '/Download/';
            if ( environment.isIosPlatform ) {
                filePath = this.file.documentsDirectory;
            }

            const fileName = a.name.replace(' ', '_').substr(0, 90);

            this.file.writeFile(filePath, fileName + '.pdf', blob, { replace: true }).then((fileEntry: FileEntry) => {
                // this.presentToast('Si utiliza iphone guarde el archivo, caso contrario revise en la carpeta Documentos.');
                this.fileOpener.open(fileEntry.toURL(), 'application/pdf')
                    .then()
                    .catch(err => this.presentToast('Error al abrir el archivo, revise en la carpeta de descargas'));
                })
                .catch((err) => {
                    this.presentToast('Error al almacenar el archivo: ' + JSON.stringify(err));
                    throw err;
                });

        });
    }

    async presentToast(msg) {
        const toast = await this.toastController.create({
            message: msg,
            duration: 5000,
            position: 'top'
        });
        toast.present();
    }

}
