import { Component, OnInit } from '@angular/core';
import {ModalController, NavParams} from '@ionic/angular';
import {RestService} from '../../../service/rest.service';
import {Vote, Voting, VotingPoliticalParty} from '../../../interfaces/model';

@Component({
    selector: 'app-modal-statistics',
    templateUrl: './modal-statistics.page.html',
    styleUrls: ['./modal-statistics.page.scss'],
})
export class ModalStatisticsPage implements OnInit {

    voting: Voting;
    votingsByPoliticalParty: VotingPoliticalParty[] = [];

    votesYes: VotingPoliticalParty[] = [];
    votesNo: VotingPoliticalParty[] = [];
    votesAbstention: VotingPoliticalParty[] = [];
    votesWhite: VotingPoliticalParty[] = [];

    colorYes = '#00FF00';
    colorNo = '#FF0000';
    colorAbstention = '#005EFF';
    colorWhite = '#FFFFFF';

    totalYes = 0;
    totalNo = 0;
    totalAbstention = 0;
    totalWhite = 0;

    constructor(private modalController: ModalController,
                private navParams: NavParams,
                private rest: RestService) { }

    ngOnInit() {
        this.voting = this.navParams.data.voting;
        this.getStatistics();
        // this.getVotes();
    }
    private getStatistics() {
        this.rest.getVotingVotesByPoliticalParty({
            votingId: this.voting.votingId
        }).subscribe( response => {
                this.votingsByPoliticalParty = response;
                this.votingsByPoliticalParty.forEach(data => {
                    switch (data.description) {
                        case 'SI':
                            this.votesYes.push(data);
                            break;
                        case 'NO':
                            this.votesNo.push(data);
                            break;
                        case 'ABSTENCION':
                            this.votesAbstention.push(data);
                            break;
                        case 'BLANCO':
                            this.votesWhite.push(data);
                            break;
                    }
                });
                this.votesYes.forEach(data => {
                    this.totalYes += data.amount;
                });
                this.votesNo.forEach(data => {
                    this.totalNo += data.amount;
                });
                this.votesAbstention.forEach(data => {
                    this.totalAbstention += data.amount;
                });
                this.votesWhite.forEach(data => {
                    this.totalWhite += data.amount;
                });
            }
        );
    }

    async closeModal() {
        await this.modalController.dismiss();
    }

}
