import {Component, OnDestroy, OnInit} from '@angular/core';
import * as Hls from 'hls.js';
import {ScreenOrientation} from '@ionic-native/screen-orientation/ngx';
import {Router} from '@angular/router';
import {ModalController} from '@ionic/angular';
import {environment} from "../../../../environments/environment";

@Component({
  selector: 'app-tvonline',
  templateUrl: './tvonline.page.html',
  styleUrls: ['./tvonline.page.scss'],
})
export class TvonlinePage implements OnInit, OnDestroy {
    url = environment.urlTvOnline;

  constructor(private screenOrientation: ScreenOrientation,
              private router: Router,
              private modalController: ModalController) { }

  ngOnInit() {
      // this.screenOrientation.lock(this.screenOrientation.ORIENTATIONS.LANDSCAPE);
      this.hlsjs();
  }

  hlsjs() {
      let video: HTMLVideoElement;
      video = document.getElementById('video') as unknown as HTMLVideoElement;
      const config = {
          autoStartLoad: true,
          startPosition: -1,
      };
      if ( Hls.isSupported() ) {
          const hls1 = new Hls(config);
          hls1.loadSource(this.url);
          hls1.attachMedia(video);
          hls1.on(Hls.Events.MANIFEST_PARSED, function() {});
      } else if (video.canPlayType('application/vnd.apple.mpegurl')) {
          video.src = this.url;
          video.addEventListener('loadedmetadata',function() {
              video.play();
          });
      } else {
          alert('HLS.js no soportado.');
      }
  }

    ngOnDestroy(): void {
        //this.screenOrientation.lock(this.screenOrientation.ORIENTATIONS.PORTRAIT);
        //this.screenOrientation.unlock();
    }

    close(): void {
        //this.screenOrientation.lock(this.screenOrientation.ORIENTATIONS.PORTRAIT);
        //this.screenOrientation.unlock();
        this.router.navigate(['home']);
    }

    async closeModal() {
        await this.modalController.dismiss();
    }
}

