import {
    HttpErrorResponse,
    HttpEvent,
    HttpHandler, HttpHeaders,
    HttpInterceptor,
    HttpRequest,
    HttpResponse
} from '@angular/common/http';
import {Observable, throwError} from 'rxjs';
import {Injectable} from '@angular/core';
import { ToastController } from '@ionic/angular';
import {catchError, flatMap, map} from 'rxjs/operators';
import {LoadingService} from '../service/loading.service';
import {LoginService} from '../service/login.service';
import {environment} from "../../environments/environment";

@Injectable({
    providedIn: 'root'
})
export class RequestInter implements HttpInterceptor {
    private urlApp = environment.urlRestApp;

    constructor(public toastController: ToastController,
                private loadingService: LoadingService,
                private loginService: LoginService) {}

    intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        const token = localStorage.getItem('token');
        let loader = true;

        // console.log(request.url);

        if ( request.url.includes('auth/login') ) {
            loader = false;
            request = request.clone({
                headers: new HttpHeaders({
                    'Content-Type':  'application/x-www-form-urlencoded'
                })
            });
        }

        if (token && !request.url.includes('auth/login') && request.url.startsWith(this.urlApp)) {
            request = request.clone({
                headers: new HttpHeaders({
                    'Authorization': token,
                    'Content-Type':  'application/json'
                })
            });
        }

        if ( request.url.includes('getPhoto')
            || request.url.includes('themesResource/find1?agendaId')
            || request.url.includes('FACEBOOK_LIVE_ID')
            || request.url.includes('facebookLiveResource/getAll')
            || request.url.includes('assemblyMembersResource/getPhoto') ) {
            loader = false;
        }

        if (loader) {
            this.loadingService.presentLoader();
        }
        return next.handle(request).pipe(
            map((event: HttpEvent<any>) => {
                if (loader) {
                    this.loadingService.dismissLoader();
                }
                if (event instanceof HttpResponse) {
                    // console.log('Interceptor Event--->>>', JSON.stringify(event));
                }
                return event;
            }),
            catchError((error: HttpErrorResponse) => {
                if (loader) {
                    this.loadingService.dismissLoader();
                }
                // console.log('Interceptor error: ' + JSON.stringify(error));
                if (error.status === 401) {
                    if (error.error.success === false) {
                        // this.presentToast('Login failed');
                    } else {
                        return this.loginService.loginApp().pipe(
                            flatMap(respAuth => {
                                localStorage.setItem('token', respAuth.token);
                                request = request.clone({
                                    headers: new HttpHeaders({
                                        'Authorization': respAuth.token,
                                        'Content-Type':  'application/json'
                                    })
                                });
                                return next.handle(request);
                            })
                        );
                    }
                } else {
                    return throwError(error);
                }
            }));

    }

    async presentToast(msg) {
        const toast = await this.toastController.create({
            message: msg,
            duration: 2000,
            position: 'top'
        });
        toast.present();
    }
}
