import { ActionReducerMap } from '@ngrx/store';
import * as configuracionLista from './reducers/favSessions/session.reducer';
import * as configuracionListaTheme from './reducers/favThemes/theme.reducer';
import {ListaModel} from './reducers/favSessions/session.model';
import {ListaModelTheme} from './reducers/favThemes/theme.model';

export interface AppState {
    listaConfiguracion: ListaModel[];
    listaThemes: ListaModelTheme[];
}
export const appReducer: ActionReducerMap<AppState> = {
    listaConfiguracion: configuracionLista.ConfigListaReducer,
    listaThemes: configuracionListaTheme.ConfigListaReducerTheme,
};