import {Component, Input, OnInit} from '@angular/core';
import {ModalController} from '@ionic/angular';
import {VotingAssembly} from '../../../interfaces/model';
import {RestService} from '../../../service/rest.service';

@Component({
    selector: 'app-modal-asambleista-quorums',
    templateUrl: './modal-asambleista-quorums.page.html',
    styleUrls: ['./modal-asambleista-quorums.page.scss'],
})
export class ModalAsambleistaQuorumsPage implements OnInit {
    @Input() assemblyId: number;
    @Input() assemblyName: string;
    @Input() periodId: number;
    quorumList: VotingAssembly[] = [];
    existQuorums: boolean;
    offset: number;
    sessionFilter: string = '';

    constructor(public modalController: ModalController, private rest: RestService) { }

    ngOnInit() {
        this.existQuorums = false;
        this.offset = 0;
        this.getQuorums();
    }

    public loadData(event) {
        setTimeout(() => {
            this.getQuorums(event);
        }, 500);
    }

    getQuorums(event?) {
        if(this.periodId === null || this.periodId === undefined){
            this.periodId = 0;
        }

        if(this.sessionFilter === null || this.sessionFilter === undefined
        ){
            this.sessionFilter = '';
        }

        const rowsLimit = 30;

        if (event !== undefined) {
            this.offset += rowsLimit;
        }

        this.rest.getQuorumsByAssembly({
            periodId: this.periodId,
            assemblyMemberId: this.assemblyId,
            session: this.sessionFilter,
            offset: this.offset,
            limit: rowsLimit
        }).subscribe(response => {
            /*this.quorumList.push(...response);
            this.quorumList.length === 0 ? this.existQuorums = true : this.existQuorums = false;
            this.quorumList.forEach(data => {
                const date = data.quorumDate.replace('[UTC]', '');
                data.votDate = new Date(date);
            });
            if (event !== undefined) {
                if (this.quorumList.length === 0) {
                    event.target.disabled = true;
                    return;
                }
                if (event) {
                    event.target.complete();
                }
            }*/

            if (response.length > 0) {
                if (event !== undefined) {
                    this.quorumList.push(...response);
                    this.quorumList.forEach(data => {
                        const date = data.proposalDescription.replace('', '');
                        data.votDate = new Date(date);
                    });
                    if (event) {
                        event.target.complete();
                    }
                } else {
                    this.quorumList = response;
                    this.quorumList.forEach(data => {
                        const date = data.proposalDescription.replace('', '');
                        data.votDate = new Date(date);
                    });
                }
            } else {
                this.existQuorums = true;
                if (response.length === 0) {
                    event.target.disabled = true;
                }
            }

        });
    }

    modalClose() {
        this.modalController.dismiss({
            'dismissed': true
        });
    }

    cleanThemeFilter(){
        this.sessionFilter = '';
        this.getQuorums(undefined);
    }
}
