import {ListaModelTheme} from '../favThemes/theme.model';

export class ListaModel {
    id: number;
    number: number;
    startDate: string;
    tipo: string;
    date?: Date;
    list: AuxiliaryList[];
    initialDate: string;
    initialTime: string;
    constructor(idIn, numberIn, startDateIn, tipoIn, dateIn, listIn, initialDateIn, initialTimeIn) {
        this.id = idIn,
        this.number = numberIn,
        this.tipo = tipoIn;
        this.startDate = startDateIn;
        this.date = dateIn;
        this.list = listIn;
        this.initialDate = initialDateIn;
        this.initialTime = initialTimeIn;
    }
}
export interface AuxiliaryList {
    id: number;
    description: string;
    startdate: Date;
    list: AuxiliaryList[];
}
export interface Action {
    type: string;
    text: ListaModel[];
}