import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { RouteReuseStrategy } from '@angular/router';

import {IonicModule, IonicRouteStrategy} from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';
import { RestService } from './service/rest.service';
import { EnvironmentService } from './service/environment.service';
import { AppComponent } from './app.component';
import { AppRoutingModule } from './app-routing.module';
import {ModalAsambleistaPage} from './components/modal/modal-asambleista/modal-asambleista.page';
import {ModalAttachmentPage} from './components/modal/modal-attachment/modal-attachment.page';
import {ModalVotingDetailPage} from './components/modal/modal-voting-detail/modal-voting-detail.page';
import {HTTP_INTERCEPTORS, HttpClientModule} from '@angular/common/http';
import {RequestInter} from './interceptors/request.inter';
import { ComponentsModule } from './components/components.module';
import {ModalOrderAprovedPage} from './components/modal/modal-order-aproved/modal-order-aproved.page';
import {ModalInterveningPage} from './components/modal/modal-intervening/modal-intervening.page';
import {YoutubePage} from './components/external/youtube/youtube.page';
import {FacebookPage} from './components/external/facebook/facebook.page';
import {StoreModule} from '@ngrx/store';
import {StoreDevtoolsModule} from '@ngrx/store-devtools';
import {environment} from '../environments/environment';
import {appReducer} from './app.reducers';
import { File } from '@ionic-native/file/ngx';
import { FileOpener } from '@ionic-native/file-opener/ngx';
import {ModalStatisticsPage} from './components/modal/modal-statistics/modal-statistics.page';
import {SocialSharing} from '@ionic-native/social-sharing/ngx';
import { CallNumber } from '@ionic-native/call-number/ngx';
import { LocalNotifications } from '@ionic-native/local-notifications/ngx';
import { ScreenOrientation } from '@ionic-native/screen-orientation/ngx';
import {InterveningDetailAssemblymanPage} from './components/modal/intervening-detail-assemblyman/intervening-detail-assemblyman.page';
import {VotingDetailAssemblymanPage} from './components/modal/voting-detail-assemblyman/voting-detail-assemblyman.page';
import {ModalAsambleistaQuorumsPage} from './components/modal/modal-asambleista-quorums/modal-asambleista-quorums.page';
import {VideoPlayer} from '@ionic-native/video-player/ngx';
import {TvonlinePage} from './components/external/tvonline/tvonline.page';
import {RadioonlinePage} from "./components/external/radioonline/radioonline.page";
// import {Media} from "@ionic-native/media/ngx";
import {FormsModule} from "@angular/forms";

@NgModule({
    declarations: [AppComponent, ModalAsambleistaPage, ModalAttachmentPage, ModalOrderAprovedPage, ModalInterveningPage,
        ModalVotingDetailPage, YoutubePage, FacebookPage, ModalStatisticsPage, InterveningDetailAssemblymanPage, VotingDetailAssemblymanPage,
        ModalAsambleistaQuorumsPage, TvonlinePage, RadioonlinePage],
    entryComponents: [ ModalAsambleistaPage, ModalAttachmentPage, ModalOrderAprovedPage, ModalInterveningPage,
        ModalVotingDetailPage, YoutubePage, ModalStatisticsPage, InterveningDetailAssemblymanPage, VotingDetailAssemblymanPage,
        ModalAsambleistaQuorumsPage, TvonlinePage, FacebookPage, RadioonlinePage],
    imports: [
        BrowserModule,
        IonicModule.forRoot(),
        StoreModule.forRoot(appReducer),
        StoreDevtoolsModule.instrument({maxAge: 25, logOnly: environment.production}),
        AppRoutingModule,
        ComponentsModule,
        HttpClientModule,
        FormsModule
    ],
    providers: [
        StatusBar,
        InAppBrowser,
        SplashScreen,
        RestService,
        EnvironmentService,
        File,
        FileOpener,
        SocialSharing,
        LocalNotifications,
        ScreenOrientation,
        CallNumber,
        VideoPlayer,
        //Media,
        { provide: RouteReuseStrategy, useClass: IonicRouteStrategy },
        {
            provide: HTTP_INTERCEPTORS,
            useClass: RequestInter,
            multi: true
        }
    ],
    bootstrap: [AppComponent]
})
export class AppModule {}
