import * as configuracionLista from './session.action';
import {ListaModel} from './session.model';
const stateInitial: ListaModel[] = (localStorage.getItem('session')) ? JSON.parse(localStorage.getItem('session')) : [];

export function ConfigListaReducer(state = stateInitial, action: configuracionLista.Actions): ListaModel[] {
    switch (action.type) {
        case configuracionLista.NAME_LIST:
            const auxNewLista = new ListaModel(action.text.id, action.text.number,
                action.text.startDate, action.text.tipo, action.text.date,
                action.text.list, action.text.initialDate, action.text.initialTime);
            if (auxNewLista.id !== undefined) {
                const arrayLista = [...state, auxNewLista];
                localStorage.setItem('session', JSON.stringify(arrayLista));
                return arrayLista;
            }
            return;
        case configuracionLista.NAME_LIST_SESSION_DELETE:
            var i = 0;
            var arrayList: ListaModel[] = [];
            const auxdeleteSession = new ListaModel(action.text.id,
                action.text.number, action.text.startDate,
                action.text.tipo, action.text.date,
                action.text.list, action.text.initialDate, action.text.initialTime);
            state.forEach(reco => {
                if (reco.startDate !== auxdeleteSession.startDate) {
                    arrayList[i] = reco;
                    i = i + 1;
                }
            });
            !(state.length === 1) ? localStorage.setItem('session', JSON.stringify(arrayList))  : localStorage.removeItem('session');

            return state.filter(data => {
                if (data.startDate !== auxdeleteSession.startDate) {

                    return data.startDate !== auxdeleteSession.startDate;
                }
            });
        default:
            return state;
    }
}
