import { Component, OnInit } from '@angular/core';
import {environment} from "../../../../environments/environment";
import {ModalController} from "@ionic/angular";

@Component({
  selector: 'app-radioonline',
  templateUrl: './radioonline.page.html',
  styleUrls: ['./radioonline.page.scss'],
})
export class RadioonlinePage implements OnInit {
  url = "http://cdn.instream.audio:9288/stream";// "http://181.198.32.153:8000/laradio"; //environment.urlRadioOnline;
  audioControl: HTMLAudioElement;

  constructor(private modalController: ModalController) { }

  ngOnInit() {
    this.reload();
  }

  reload(){
      this.url += '?dt=' + new Date().getTime();
      this.audioControl = document.getElementById('audio') as unknown as HTMLAudioElement;
      this.audioControl.src = this.url;
      this.audioControl.load();
      this.audioControl.play();
  }

   async closeModal() {
    await this.modalController.dismiss();
  }

}
