import { Component, OnInit, OnDestroy } from '@angular/core';
import { ModalController, NavParams} from '@ionic/angular';
import { DomSanitizer } from '@angular/platform-browser';
import { ScreenOrientation } from '@ionic-native/screen-orientation/ngx';
import {LoadingService} from "../../../service/loading.service";

@Component({
  selector: 'app-youtube',
  templateUrl: './youtube.page.html',
  styleUrls: ['./youtube.page.scss'],
})
export class YoutubePage implements OnInit, OnDestroy {
    myurl: any = '';

  constructor(private modalController: ModalController,
              public navParams: NavParams,
              private sanitizer: DomSanitizer,
              private screenOrientation: ScreenOrientation,
              private loadingService: LoadingService
              ) { }

  ngOnInit() {
      this.loadingService.presentLoader();
      // this.screenOrientation.lock(this.screenOrientation.ORIENTATIONS.LANDSCAPE);
      this.myurl = this.sanitizer.bypassSecurityTrustResourceUrl(this.navParams.data.myurl);
  }

    async closeModal() {
        await this.modalController.dismiss();
    }

    ngOnDestroy(): void {
        // this.screenOrientation.lock(this.screenOrientation.ORIENTATIONS.PORTRAIT);
        // this.screenOrientation.unlock();
    }

    closeLoader(){
        this.loadingService.dismissLoader();
    }
}
