import {Component, Input, OnInit} from '@angular/core';
import {ModalController} from '@ionic/angular';
import {RestService} from '../../../service/rest.service';
import {InterventionByTheme} from '../../../interfaces/model';
import {YoutubePage} from '../../external/youtube/youtube.page';

@Component({
  selector: 'app-intervening-detail-assemblyman',
  templateUrl: './intervening-detail-assemblyman.page.html',
  styleUrls: ['./intervening-detail-assemblyman.page.scss'],
})
export class InterveningDetailAssemblymanPage implements OnInit {
    @Input() period: number;
    @Input() idAssembly: number;
    @Input() nameAssembly: string;
    banSend: boolean [] = [];
    ban = false;
    interveningList: InterventionByTheme[];
    datePartsTime: string [];
    textIncomplete: string [] = [];
    themeFilter: string = '';
    i = 0;
    j = 0;
    constructor(public modalController: ModalController,
                private rest: RestService) { }

  ngOnInit() {
      this.getInterveningDetail();
  }

  modalClose() {
        this.modalController.dismiss({
            'dismissed': true
        });
  }

    public loadData(event) {
        setTimeout(() => {
            this.getInterveningDetail(event);
        }, 500);
    }

    banAction(pos) {
        this.banSend[pos] = !this.banSend[pos];
    }

  getInterveningDetail(event?) {
      if(this.period === null || this.period === undefined){
          this.period = 0;
      }

      if(this.themeFilter === null || this.themeFilter === undefined){
          this.themeFilter = '';
      }

      this.ban = false;
      this.j = 0;
      const incre = 30;
      if (event !== undefined) {
          this.i = this.i + incre;
      }
      this.rest.getDetailIntervening({
          periodId: this.period,
          assemblyMemberId: this.idAssembly,
          description: this.themeFilter,
          offset: this.i,
          limit: incre
      }).subscribe(response => {
          /*this.interveningList = response;
          if (this.interveningList.length === 0) {
              this.ban = true;
          }
          this.interveningList.forEach(data => {
          if (data.requestTime === undefined) {
                  data.requestTime = ' ';
          }
          this.datePartsTime = data.requestTime.split('[');
          data['dateConvert'] = new Date(this.datePartsTime[0]).toDateString();
          data['timeStart'] = new Date( this.datePartsTime[0]).toLocaleTimeString();
          data['timeElapsed'] = this.getFormatHours(data.elapsed);
          this.banSend[this.i] = false;
          this.textIncomplete[this.i] = data['description'].substring(0, 100);
          this.i++;*/
          if (response.length > 0) {
              if (event !== undefined) {
                  this.interveningList.push(...response);
                  this.interveningList.forEach(data => {
                      if (data.requestTime === undefined) {
                          data.requestTime = ' ';
                      }
                      this.datePartsTime = data.requestTime.split('[');
                      data['dateConvert'] = new Date(this.datePartsTime[0]).toDateString();
                      data['timeStart'] = new Date( this.datePartsTime[0]).toLocaleTimeString();
                      data['timeElapsed'] = this.getFormatHours(data.elapsed);
                      this.banSend[this.j] = false;
                      this.textIncomplete[this.j] = data['description'].substring(0, 100);
                      this.j++;
                  });
                  if (event) {
                      event.target.complete();
                  }
              } else {
                  this.interveningList = response;
                  this.interveningList.forEach(data => {
                      if (data.requestTime === undefined) {
                          data.requestTime = ' ';
                      }
                      this.datePartsTime = data.requestTime.split('[');
                      data['dateConvert'] = new Date(this.datePartsTime[0]).toDateString();
                      data['timeStart'] = new Date( this.datePartsTime[0]).toLocaleTimeString();
                      data['timeElapsed'] = this.getFormatHours(data.elapsed);
                      this.banSend[this.j] = false;
                      this.textIncomplete[this.j] = data['description'].substring(0, 100);
                      this.j++;
                  });
              }
          } else {
              this.ban = true;
              if (response.length === 0) {
                  event.target.disabled = true;
              }
          }
      },error1 => {
          console.log('Ha ocurrido un error');
      });
  }
    private getFormatHours(input) {
        let totalHours, totalMinutes, totalSeconds, hours, minutes, seconds, result = '';
        totalSeconds = input / 1000;
        totalMinutes = totalSeconds / 60;
        totalHours = totalMinutes / 60;

        seconds = Math.floor(totalSeconds) % 60;
        minutes = Math.floor(totalMinutes) % 60;
        hours = Math.floor(totalHours) % 60;

        if (hours !== 0) {
            result += hours + 'hora,';

            if (minutes.toString().length === 1) {
                minutes = '0' + minutes;
            }
        }

        result += minutes + 'min,';

        if (seconds.toString().length === 1) {
            seconds = '0' + seconds;
        }

        result += seconds + 'sec';
        return result;
    }
    async openDetail(o: InterventionByTheme) {
        // onclick="window.open('https://www.facebook.com/2019seagamesph/videos/520332335241234/', '_blank', 'location=no'); return false;"
        const modal = await this.modalController.create({
            component: YoutubePage,
            componentProps: {
                "myurl": o.youtubeurl
            }
        });

        modal.onDidDismiss().then((dataReturned) => {
            if (dataReturned !== null) { }
        });

        return await modal.present();
    }

    cleanThemeFilter(){
        this.themeFilter = '';
        this.getInterveningDetail(undefined);
    }
}
