import { Component } from '@angular/core';

import {MenuController, ModalController, Platform, ToastController} from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import {InAppBrowser, InAppBrowserOptions} from '@ionic-native/in-app-browser/ngx';
import {LoginService} from './service/login.service';
import {Properties} from './properties/properties';
import {environment} from '../environments/environment';
import {RestService} from './service/rest.service';
import {EnvironmentService} from './service/environment.service';
import {TvonlinePage} from './components/external/tvonline/tvonline.page';
import {FacebookLive} from "./interfaces/model";
import {FacebookPage} from "./components/external/facebook/facebook.page";
import {LoadingService} from "./service/loading.service";

@Component({
    selector: 'app-root',
    templateUrl: 'app.component.html',
    styleUrls: ['app.component.scss']
})
export class AppComponent {

    public timeBegan = new Date();
    public timeStopped = null;
    public stoppedDuration = 0;
    public started = null;
    public running = false;
    public blankTime = '00:00.000';
    public time = '00:00.000';
    // Radio
    public showRadio: boolean = false;
    private urlRadio = environment.urlRadioOnline;
    private audioControl: HTMLAudioElement;
    env = environment;

    constructor(
        private platform: Platform,
        private splashScreen: SplashScreen,
        private statusBar: StatusBar,
        private iab: InAppBrowser,
        private restLogin: LoginService,
        public properties: Properties,
        private rest: RestService,
        private environmentService: EnvironmentService,
        private modalController: ModalController,
        public toastController: ToastController,
        public menuCtrl: MenuController,
        private loadingService: LoadingService
    ) {
        this.initializeApp();
        this.goApp();
    }

    initializeApp() {
        this.platform.ready().then(() => {
            this.audioControl = document.getElementById('audio') as unknown as HTMLAudioElement;
            this.statusBar.styleBlackOpaque();
            this.splashScreen.hide();
            if ( this.platform.is('ios') ) {
                environment.isIosPlatform = true;
            }
            // this.statusBar.overlaysWebView(true);
            this.statusBar.backgroundColorByHexString('#1A2C66');
            this.getTransmissions(false);
            this.openRadio();
            this.start();
        });
    }

    goApp() {
        this.restLogin.loginApp().subscribe(
            response => { localStorage.setItem('token', response.token); },
            error => { console.error('Error login >>' + JSON.stringify(error) ); }
        );
    }

    openPageWhithOutrlBar(url: string){
        const options: InAppBrowserOptions = {
            zoom: 'no',
            location: 'no',
            toolbar: 'no'
        };
        const browser = this.iab.create(url,'_blank', options);
        browser.show();
    }

    openPage(url: string) {
        window.open(url, '_system');
    }

    goLiveSession() {
        this.getTransmissions(true);
    }

    async goLiveTv() {
        const modal = await this.modalController.create({
            component: TvonlinePage
        });
        return await modal.present();
    }

    goRadio(){
        this.environmentService.setShowRadio(true);
    }

    openRadio() {
        this.environmentService.getShowRadio().subscribe(value => {
            if(this.showRadio && value){
                this.presentToast("Panel de la Radio se encuentra activo en el menú lateral");
                this.menuCtrl.open();
                return;
            }
            if(value) {
                this.reload();
            }
            if( !value ){
                this.audioControl = document.getElementById('audio') as unknown as HTMLAudioElement;
                this.audioControl.pause();
                this.showRadio = false;
            }
        });
    }

    reload(){
        this.loadingService.presentLoader();
        this.urlRadio += '?dt=' + new Date().getTime();
        this.audioControl = document.getElementById('audio') as unknown as HTMLAudioElement;
        this.audioControl.src = this.urlRadio;
        this.audioControl.load();
        this.audioControl.play();
        this.audioControl.addEventListener('error', ()=> {
            this.presentToast("Emisión de la Radio no disponible, intente más tarde.");
            this.closeRadio();
            this.loadingService.dismissLoader();
        });
        this.audioControl.addEventListener('canplay', ()=> {
            this.loadingService.dismissLoader();
            this.presentToast("Panel de la Radio activado en el menú lateral");
            this.menuCtrl.open();
            this.showRadio = true;
        });
    }

    closeRadio() {
        this.environmentService.setShowRadio(false);
    }

    start() {
        if (this.running) {
          return;
        }
        if (this.timeBegan === null) {
            this.reset();
            this.timeBegan = new Date();
        }

        if (this.timeStopped !== null) {
            const newStoppedDuration = (+new Date() - this.timeStopped)
            this.stoppedDuration = this.stoppedDuration + newStoppedDuration;
        }
        this.started = setInterval(this.clockRunning.bind(this), 10);
        this.running = true;
    }

    stop() {
        this.running = false;
        this.timeStopped = new Date();
        clearInterval(this.started);
    }

    reset() {
        this.running = false;
        clearInterval(this.started);
        this.stoppedDuration = 0;
        this.timeBegan = null;
        this.timeStopped = null;
        this.time = this.blankTime;
    }

    zeroPrefix(num, digit) {
        let zero = '';
        for (let i = 0; i < digit; i++) {
            zero += '0';
        }
        return (zero + num).slice(-digit);
    }

    clockRunning() {
        const currentTime = new Date();
        const timeElapsed = new Date(Number(currentTime) - Number(this.timeBegan) - Number(this.stoppedDuration));
        const hour = timeElapsed.getUTCHours();
        const min = timeElapsed.getUTCMinutes();
        const sec = timeElapsed.getUTCSeconds();
        const ms = timeElapsed.getUTCMilliseconds();
        this.time =
            this.zeroPrefix(hour, 2) + ':' +
            this.zeroPrefix(min, 2) + ':' +
            this.zeroPrefix(sec, 2) + '.' +
            this.zeroPrefix(ms, 3);

        if (sec === 60) {
            // this.getTransmissions(false);
            this.reset();
            this.start();
        }
    }

    getTransmissions(open: boolean) {
        this.rest.getFacebookLive().subscribe(response => {
            let l: FacebookLive[] = [];
            let l1: FacebookLive[] = [];
            l = response;
            for( const o of l ){
                if ( o.url.length > 0 ) {
                  l1.push(o);
                }
            }
            if( l1.length > 0 ) {
                this.environmentService.setFacebookList(l1);
            }else {
                this.environmentService.setFacebookList(null);
            }
            if(open){
                this.verifyActiveTransmissions();
            }
        }, error => {
            this.environmentService.setFacebookList(null);
        });
    }

    verifyActiveTransmissions() {
        this.environmentService.getFacebookList().subscribe(value => {
            if( value != null && value.length > 0 ) {
                this.openTransmissionPage();
            } else {
                this.presentToast('Actualmente no contamos con una transmisión en vivo');
            }
        });
    }

    async openTransmissionPage(){
        const modal = await this.modalController.create({
            component: FacebookPage
        });

        modal.onDidDismiss().then((dataReturned) => {
            if (dataReturned !== null) { }
        });

        return await modal.present();
    }

    async presentToast(msg) {
        const toast = await this.toastController.create({
            message: msg,
            duration: 3000,
            position: 'top'
        });
        toast.present();
    }
}
