import {Component, Input, OnInit} from '@angular/core';
import { ModalController } from '@ionic/angular';
import {AssemblyMan, MeetingGroup, Period, Vote} from '../../../interfaces/model';
import { RestService } from '../../../service/rest.service';
import {InterveningDetailAssemblymanPage} from '../intervening-detail-assemblyman/intervening-detail-assemblyman.page';
import {VotingDetailAssemblymanPage} from '../voting-detail-assemblyman/voting-detail-assemblyman.page';
import {ModalAsambleistaQuorumsPage} from '../modal-asambleista-quorums/modal-asambleista-quorums.page';

@Component({
  selector: 'app-modal-asambleista',
  templateUrl: './modal-asambleista.page.html',
  styleUrls: ['./modal-asambleista.page.scss'],
})
export class ModalAsambleistaPage implements OnInit {
    @Input() assembly: AssemblyMan[];
    @Input() periodActive: any;
    commissions: string[] = [];

  constructor(public modalController: ModalController, private rest: RestService) { }

  ngOnInit() {
      if ( this.assembly[0] && this.assembly[0].id > 0 ) {
          this.rest.getCommissionsAssemblyman({
              assemblyMemberId: this.assembly[0].id
          }).subscribe( response => {
              this.commissions = response;
              }
          );
      }
  }
  async changeIntervening(item: AssemblyMan) {
        const modal = await this.modalController.create({
            component: InterveningDetailAssemblymanPage,
            componentProps: {
                'period': this.periodActive,
                'idAssembly': item.id,
                'nameAssembly': item.lastName + ' ' + item.firstName
            }
        });
        return await modal.present();
  }
    async changeVoting(item: AssemblyMan) {
      if(this.periodActive ===  undefined){
          this.periodActive = '0';
      }
        const modal = await this.modalController.create({
            component: VotingDetailAssemblymanPage,
            componentProps: {
                'period': this.periodActive,
                'idAssembly': item.id,
                'nameAssembly': item.lastName + ' ' + item.firstName
            }
        });
        return await modal.present();
    }

    async getQuorums(item: AssemblyMan) {
        if(this.periodActive ===  undefined){
            this.periodActive = '0';
        }
        const modal = await this.modalController.create({
            component: ModalAsambleistaQuorumsPage,
            componentProps: {
                'assemblyId': item.id,
                'assemblyName': item.lastName + ' ' + item.firstName,
                'periodId': this.periodActive
            }
        });
        return await modal.present();
    }

  modalClose() {
      this.modalController.dismiss({
          'dismissed': true
      });
  }
  /*getPeriod() {
        this.rest.getPeriod().subscribe(respo => {
            this.periodActive =  respo;
        });
  }*/
}
