import { Component, OnInit } from '@angular/core';
import {ModalController, NavParams} from '@ionic/angular';
import {RestService} from '../../../service/rest.service';
import {OrderApproved, Sessions} from '../../../interfaces/model';
import {DatePipe} from '@angular/common';

@Component({
  selector: 'app-modal-order-aproved',
  templateUrl: './modal-order-aproved.page.html',
  styleUrls: ['./modal-order-aproved.page.scss'],
})
export class ModalOrderAprovedPage implements OnInit {
  listOrder: OrderApproved[];
  sessionObject: Sessions;

  constructor(private modalController: ModalController,
              private navParams: NavParams,
              private rest: RestService) { }

  ngOnInit() {
      this.sessionObject = this.navParams.data.sessionObject;
      this.getOrderApproved();
  }
  async closeModal() {
      await this.modalController.dismiss();
  }
  private getOrderApproved() {
      const datePipe: DatePipe = new DatePipe('en-EN');
      const parts = this.sessionObject.startDate.split('-');
      const myDate = new Date(parseInt(parts[0]), parseInt(parts[1]) - 1, parseInt(parts[2]));
      let dateImport = myDate.toDateString();
      dateImport = datePipe.transform(dateImport, 'yyyy-MM-dd');
      this.rest.getOrderApproved({
         agendaId: this.sessionObject.id,
         date: dateImport,
      }).subscribe(response => {
         this.listOrder = response;
      });
  }

    formatDate(s: string) {
        s = s.replace('[UTC]', '');
        return new Date(s);
    }

}
