import * as configuracionListaTheme from './theme.action';
import {ListaModelTheme} from './theme.model';
const stateInitial: ListaModelTheme[] = (localStorage.getItem('theme')) ? JSON.parse(localStorage.getItem('theme')) : [];

export function ConfigListaReducerTheme(stateTheme = stateInitial, action: configuracionListaTheme.Actions): ListaModelTheme[] {

    switch (action.type) {
        case configuracionListaTheme.NAME_LIST_THEME:
            const auxNewListaTheme = new ListaModelTheme(action.text.agendaId,
                action.text.agendaNumber,
                action.text.agendaStartDate,
                action.text.approvedOrder, action.text.dates, action.text.description, action.text.id);
            if (auxNewListaTheme.agendaId !== undefined) {
                const arrayListaTheme = [...stateTheme, auxNewListaTheme];
                localStorage.setItem('theme', JSON.stringify(arrayListaTheme));
                return arrayListaTheme;
            }
            return;
        case configuracionListaTheme.NAME_LIST_THEME_DELETE:
            var i = 0;
            var arrayList: ListaModelTheme[] = [];
            const auxdeleteTheme = new ListaModelTheme(action.text.agendaId,
                action.text.agendaNumber, action.text.agendaStartDate,
                action.text.approvedOrder, action.text.dates,
                action.text.description, action.text.id);
            stateTheme.forEach(reco => {
                if (reco.description !== auxdeleteTheme.description) {
                    arrayList[i] = reco;
                    i = i + 1;
                }
            });
            !(stateTheme.length === 1) ? localStorage.setItem('theme', JSON.stringify(arrayList)) : localStorage.removeItem('theme');

            return stateTheme.filter(data => {
                if (data.description !== auxdeleteTheme.description) {

                    return data.description !== auxdeleteTheme.description;
                }
            });
        default:
            return stateTheme;
    }
}